html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.333;
  z-index: 0;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  transition: background-color $animation;
}

.main-section {
  padding-top: 20px;
  padding-bottom: 40px;
  @media screen and (min-width: $medium) {
    padding-top: 60px;
    padding-bottom: 50px;
  }
  @media screen and (min-width: $large) {
    padding-bottom: 46px;
  }
  @media screen and (min-width: $full) {
    padding: 50px 0;
  }
}

.container {
  @media screen and(max-width: $small) {
    width: 100%;
    // padding: 0 10px;
  }
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;

  @media screen and(min-width: $medium) {
    width: 768px;
    padding: 0 75px;
  }
  @media screen and(min-width: $large) {
    width: 1024px;
    padding: 0 71px;
  }
  @media screen and(min-width: $full) {
    width: 1400px;
    padding: 0 70px;
  }

  &.notice {
    background:url("../images/background/NoRecordFound.png") center no-repeat;
    // background-image: linear-gradient(to right, rgba(0,0,0,0.1),rgba(0,0,0,0.2)),
    //   url("../images/background/nocontent.png");
    height: 300px;
    width: 300px;
    border: 1px transparent solid;
    border-radius: 3%;
    @media screen and (min-width:768px){
      height: 400px;
      width: 600px;
    }
    

    @media screen and (min-width: 1024px) {
      height: 500px;
      width: 700px;
    }
  }
}

body.light {
  background: $mainBackgroundColor;
}

body.dark {
  background: $darkThemeColor;
}

body.dark h2 {
  color: $secondaryTextColor;
}

body.dark footer p {
  color: $secondaryTextColor;
}

*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding-left: 0;
}

li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden--visually {
  border: 0;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
